                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{'active':activeKey==2}" data-bs-toggle="tab"
                            data-bs-target="#tab-notes-content" role="tab" href="javascript:void(0)"
                            id="tab-notes" v-on:click="loadData('notes')">
                            <em class="fa fa-file-text" aria-hidden="true" aria-selected="false"></em>{{$t('NOTE')}}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{'active':activeKey==3}"
                            data-bs-toggle="#tab-attachment-content" role="tab" href="javascript:void(0)"
                            id="tab-attachment" v-on:click="loadData('attachments')">
                            <em class="fa fa-file" aria-hidden="true"  aria-selected="false"></em>{{$t('Attachments')}}
                            </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{'active':activeKey==4}"
                            data-bs-target="#tab-openactivities-content" href="javascript:void(0)"
                            id="tab-openactivities" data-bs-toggle="tab" role="tab"
                            v-on:click="loadData('activities')" aria-selected="false">
                            <em class="fa fa-calendar" aria-hidden="true"></em>{{$t('Activities')}}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{'active':activeKey==5}" data-bs-toggle="tab"
                            role="tab" href="javascript:void(0)" id="tab-emails-log"
                            v-on:click="loadData('email')" aria-selected="false">
                            <em class="fa fa fa-envelope" aria-hidden="true"></em>{{$t('Emails')}}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{'active':activeKey==6}" 
                            href="javascript:void(0)" id="tab-Contact" data-bs-toggle="tab" role="tab" 
                            v-on:click="loadData('contacts')" aria-selected="false"><em class="fa fa-users" aria-hidden="true"></em>{{$t('Contact')}}
                        </a>
                    </li>
                </ul>
            </div>
            <div class="right-content">
                    <div class="col-md-12 p-0 mb-2">
                        <div class="theme-primary partition p-actions-expand">
                            <span class="p-name text-white" id="span_ForSelectedTabName"></span>
                            <span class="p-actions float-right">
                             
                                <RouterLink 
                                    to="/CRM/ContractListing" 
                                    class="p-action-btn text-white float-right">
                                    <em class="fa fa-angle-double-left"></em><br />{{$t('BacktoList')}}
                                </RouterLink>
                                <a 
                                    href="javascript:;" 
                                    v-bind:class="{'p-action-btn text-white':true, 'disabled':false}" 
                                    title="" 
                                    data-toggle-tooltip="tooltip"
                                     data-original-title="Card View" 
                                     v-on:click="NoteListType = 'card'" 
                                     v-if="isNotes && NoteListType == 'list'">
                                     <em class="fa fa-th-large"></em><br />{{$t('CardView')}}
                                </a>
                                <a 
                                    href="javascript:;" 
                                    v-bind:class="{'p-action-btn text-white':true, 'disabled':false}" 
                                    title="" 
                                    data-toggle-tooltip="tooltip" 
                                    data-original-title="List View" 
                                    v-on:click="NoteListType = 'list'" 
                                    v-if="isNotes && NoteListType != 'list'">
                                    <em class="fa fa-list"></em><br />{{$t('ListView')}}
                                </a>
                                <a 
                                    href="javascript:;" 
                                    v-bind:class="{'p-action-btn text-white':true, 'disabled':false}" 
                                    title="" 
                                    id="ancaddnotesloaddata" 
                                    data-toggle-tooltip="tooltip" 
                                    data-original-title="Add Notes" 
                                    v-if="isNotes" v-on:click="openShowNotePopup()">
                                    <em class="fa fa-plus"></em><br />{{$t('AddNotes')}}
                                </a>
                                <a 
                                    href="javascript:;" 
                                    v-bind:class="{'p-action-btn text-white':true}" 
                                    :title="$t('UploadFile')"
                                    v-on:click="openCRMAttachmentModal(null);"
                                    v-if="isAttachment">
                                    <em class="fa fa-plus"></em><br />{{$t('Add')}} {{$t('Attachment')}}
                                </a>
                                <a 
                                    href="javascript:void(0)" 
                                    v-bind:class="{'p-action-btn text-white btn_call':true}" 
                                    title="" 
                                    id="" 
                                    v-on:click="openCRMEmailModal(null)" 
                                    :data-original-title="$t('SendEmail')" 
                                    v-if="isSendEmail">
                                    <em class="fa fa-envelope"></em><br />{{$t('SendEmail')}}
                                </a>
                                <a 
                                    href="javascript:void(0)" 
                                    v-bind:class="{'p-action-btn text-white btn_add':true}" 
                                    title="" 
                                    id="" 
                                    v-on:click="openAddTaskModel()" 
                                    data-original-title="Add Task" 
                                    v-if="isTask">
                                    <em class="fa fa-plus"></em><br />{{$t('AddTask')}}
                                </a>
                                <a 
                                    href="javascript:void(0)" 
                                    v-bind:class="{'p-action-btn text-white btn_follow':true}" 
                                    title="" 
                                    id="" 
                                    v-on:click="openAddNewEvent()" 
                                    data-original-title="Follow Up" 
                                    v-if="isTimeline">
                                    <em class="fa fa-share"></em><br />{{$t('FollowUp')}}
                                </a>
                                <a 
                                    href="javascript:void(0)" 
                                    v-bind:class="{'p-action-btn text-white btn_call':true}" 
                                    title="" 
                                    id="" 
                                    v-on:click="openCallEvent()" 
                                    data-original-title="Schedule Call" 
                                    v-if="isTimeline">
                                    <em class="fa fa-phone"></em><br />{{$t('Schedule')}} {{$t('Call')}}
                                </a>
                                <a 
                                    href="javascript:;" 
                                    v-bind:class="{'p-action-btn text-white':true}" 
                                    title="" 
                                    id="ancmergeloaddata" 
                                    v-on:click="openCRMExistingContactModel()" 
                                    :data-original-title="$t('AssociateContact')"
                                    v-if="isContract">
                                    <em class="fa fa-plus"></em><br />{{$t('AssociateContact')}}
                                </a>
                                <!-- <a 
                                    href="javascript:;" 
                                    v-bind:class="{'p-action-btn text-white':true}" 
                                    title="" 
                                    id="ancmergeloaddata" 
                                    v-on:click="openCRMContactModal(null)" 
                                    :data-original-title="$t('AddContact')" 
                                    v-if="isContract">
                                    <em class="fa fa-user"></em><br />{{$t('AddContact')}}
                                </a> -->
                                <RouterLink 
                                    :to="editurl"
                                    class="p-action-btn text-white" 
                                    v-if="stausName != 'Closed'">
                                    <em class="fa fa-pencil"></em><br />{{$t('Edit')}}
                                </RouterLink>
                            </span>
                            <span class="collapse-head-btns">
                                <a href="javascript:;" class="collapse-btns"><em class="fa fa-angle-up text-white"></em></a>
                            </span>
                        </div>
                    </div>
                    <div class="tab-pane p-0">
                        <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
                            <div class="expand-list">
                                <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                                    <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                                </a>
                            </div>
                            <div 
                                class="tab-pane p-0 pb-2 border-0" 
                                :class="{'active show':activeKey==1}"
                                role="tabpanel" id="tab-lead-content" aria-labelledby="tab-lead-content">
                                
                                <div class="listing">
                                    <div class="row">
                                        <div class="col-md-12">
                                            <div class="card mb-3 probx">
                                                <div class="card-header">
                                                    <h2>{{$t('Details')}}</h2>
                                                    <div class="cont_right ">
                                                        <div class="bar">
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="card-body">
                                                    <div class="panel1" v-for="(group,index) in LeftViewData" :key="index">
                                                        <div class="heading-border">
                                                            <h5 class="h5 mb-2"><span>{{$t(group.group_name,'')}}</span></h5>
                                                        </div>
                                                        <div class="row">
                                                            <div class="col-md-6 col-lg-3" v-for="(item, index) in group.LeftViewData" :key="index">
                                                                <div class="form-group">
                                                                    <label>
                                                                        <strong>{{$t(item.name, item.display_name)}}:</strong>
                                                                        <span class="answer rightlabeltext ml-1" v-if="item.dt_code == 'date' || item.dt_code == 'datetime'">{{ item.value | formatDate}}</span>
                                                                        <span class="answer rightlabeltext ml-1" v-if="item.dt_code == 'decimal'"> {{isBlank(item.value)?"0.00":item.value | currency}} </span>
                                                                        <span class="answer rightlabeltext ml-1" v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options != 'Lookup'">{{ isBlank(item.value)?"--":(item.value)}}</span>
                                                                        <span class="answer rightlabeltext ml-1" v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options == 'Lookup'">{{ isBlank(item.value)?"--":(FilterDDLandNormalValue(item.value))}}</span>
                                                                    </label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="card mb-3 probx">
                                                <div class="card-header">
                                                    <h2>{{$t('Timeline')}}</h2>
                                                  
                                                </div>
                                                <div class="card-body">
                                                    <crmtimeline v-bind:timelinedata="timelinedata"></crmtimeline>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                             <div class="tab-pane fade p-0 pb-2 border-0" id="tab-notes-content" role="tabpanel"
                                    aria-labelledby="tab-notes-content" :class="{'active show':activeKey==2}">                                   
                                    <crmnote v-if="noteData" v-bind:notedata="noteData" :userid="userid"
                                         :sourceid="$route.params.id"
                                        :modulename="$route.params.moduleName"
                                        :submodulecode="$route.params.subModuleCode" v-bind:listtype="NoteListType"
                                       :showNotePopUp="isShowNotePopup"
                                        callfrom="quickViewAndNoteJS">
                                    </crmnote>
                            </div>
                            <div class="tab-pane fade p-0 pb-2 border-0" :class="{'active show':activeKey==3}"
                                    id="tab-attachment-content" role="tabpanel"
                                    aria-labelledby="tab-attachment-content">
                                    <crmattachment 
                                        v-bind:attachmentdata="attachmentData" :sourceid="$route.params.id">
                                    </crmattachment>
                            </div>
                            <div class="tab-pane fade p-0 border-0 listing" :class="{'active show':activeKey==4}"
                                id="tab-openactivities-content" role="tabpanel"
                                aria-labelledby="tab-openactivities-content">
                                <div class="table-responsive" v-if="!noRecordForTab">
                                    <table class="table table-bordered mb-0" id="table-openactivities-content" aria-describedby="activitescontent">
                                        <thead class="thead-bg">
                                            <tr>
                                                 <th scope="col"><span>{{$t('Subject')}}</span></th>
                                                 <th scope="col"><span>{{$t('Activity')}}{{$t('Type')}}</span></th>
                                                 <th scope="col"><span>{{$t('Activity')}}{{$t('ScheduleDate')}}</span></th>
                                                 <th scope="col"><span>{{$t('Activity')}}{{$t('OWNER')}}</span></th>
                                                 <th scope="col"><span>{{$t('CreatedAt')}}</span></th>
                                                 <th scope="col"><span>{{$t('Status')}}</span></th>
                                                 <th scope="col" class="text-center"><span>{{$t('Action')}}</span></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item,index) in activitiesData" :key="index">
                                                <td><a href="javascript:;"
                                                        v-on:click="openActivityModal(item);">{{item.followup_notification_subject}}</a>
                                                </td>
                                                <td>
                                                    <div v-if="item.activity_type === 'FollowUp'">
                                                        Follow up
                                                    </div>
                                                    <div v-else>
                                                        {{item.activity_type}}
                                                    </div>
                                                </td>
                                               
                                                <td>{{item.followup_notification_schedule | formatDate}}
                                                </td>
                                                <td>{{item.activity_owner}}</td>
                                                <td> {{item.created_at | formatDate}}</td>
                                                <td><span
                                                        v-bind:class="{'px-2 bg-success text-white d-block text-center': (item.status_name == 'Active'),'px-2 bg-danger text-white d-block text-center': (item.status_name == 'Inactive')}">{{item.status_name}}</span>
                                                </td>
                                                <td class="text-center single-action"><a href="javascript:;"
                                                        title="Delete" v-on:click="DeleteActivity(item);"><em                        class="fa fa-trash action-icon text-danger"></em></a>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <div v-if="noRecordForTab" class="text-center text-danger p-4 border no-record">{{$t('NoRecordfound')}}</div>
                            </div>
                            <div class="tab-pane fade border-0 p-0" :class="{ 'active show': activeKey == 5}"
                                    id="tab-emails-content" role="tabpanel" aria-labelledby="tab-emails-content">
                                    <crmsendemail v-bind:emaildata="EmailData" @GetEmailInfo="GetEmailInfo"></crmsendemail>
                            </div>
                            <div class="tab-pane fade p-0 border-0" :class="{ 'active show': activeKey == 6 }" id="tab-Contact-content"
                                                    role="tabpanel" aria-labelledby="tab-Contact-content">
                                <crmcontacttab v-bind:contactsdata="contactsData" v-bind:contactsheader="contactsHeader"
                                    :sourceid="$route.params.id" :submodulecode="$route.params.subModuleCode"
                                    :modulename="$route.params.moduleName">
                                </crmcontacttab>
                            </div>
                        </div>
                    </div>
                </div>
        </section>
        <modalattachment v-if="showAttachmentModal" @close="showAttachmentModal = false" :userid="userid"
            :companyid="companyId" :id="$route.params.id" :modulename="$route.params.moduleName"
            :submodulecode="$route.params.subModuleCode" :containername="containername" imagesizedata="">
            <h5 slot="header" class="modal-title">$t('Attachments')</h5>
        </modalattachment>
        <modaltask v-if="showAddTaskModal" @close="showAddTaskModal = false, modalTaskItem = null"
            :modalTaskItem="modalTaskItem">
            <h5 slot="header" class="modal-title">{{ $t('AddTask') }}</h5>
        </modaltask>
        <modalevent v-if="showAddEventModal" @close="showAddEventModal = false, modalFollowUpItem = null"
            :modalFollowUpItem="modalFollowUpItem" v-bind:modulename="$route.params.moduleName"
            v-bind:submodulecode="$route.params.subModuleCode" :sourceid="$route.params.id">
            <h5 slot="header" class="modal-title">{{ $t('FollowUp') }}</h5>
        </modalevent>
        <modalcall v-if="showCallModal" :modalCallItem="modalCallItem"
            @close="showCallModal = false, modalCallItem = null">
            <h5 slot="header" class="modal-title">{{ $t('Schedule') }} {{ $t('Call') }}</h5>
        </modalcall>
        <modalemail v-if="sendEmailModal" @close="sendEmailModal = false" :email="email" :userid="userid"
            :companyid="companyId" :id="$route.params.id" :modulename="$route.params.moduleName"
            :submodulecode="$route.params.subModuleCode" :containername="containername" :EmailInfo="EmailInfo">
            <h5 slot="header" class="modal-title">{{ $t("Email") }}</h5>
        </modalemail>
        <modalexistingcontact v-if="showExistingContactModal" :sourceid="$route.params.id"
            :submodulecode="$route.params.subModuleCode" :modulename="$route.params.moduleName" :userinfo="userid"
            :companyid="companyId" @close="showExistingContactModal = false" sourcename="Lead">
            <h5 slot="header" class="modal-title">{{ $t('Contacts') }}</h5>
        </modalexistingcontact>
        <modalcontact v-if="showContactModal" @close="showContactModal = false" :sourceid="$route.params.id"
            :submodulecode="$route.params.subModuleCode" :modulename="$route.params.moduleName" :userinfo="userid" :iscontactDetails="iscontactDetails"
            :companyid="companyId" sourcename="Lead">
        </modalcontact>
    </div>
</template>

<script>
import ContractDataService from "../../../services/DataService";
import crmtimeline from "../../../components/Common/Modal/crmtimeline.vue";
import crmnote from "../../../components/Common/Modal/crmnote.vue";
import crmattachment from "../../../components/Common/Modal/crmattachment.vue";
import crmsendemail from "../../../components/Common/Modal/crmsendemail.vue";
import crmcontacttab from "../../../components/Common/Modal/crmcontacttab.vue";
import modaltask from "../../../components/Common/Modal/modaltask.vue";
import modalcall from "../../../components/Common/Modal/modalcall.vue";
import modalevent from "../../../components/Common/Modal/modalevent.vue";
import modalemail from "../../../components/Common/Modal/modalemail.vue";
import modalexistingcontact from "../../../components/Common/Modal/modalexistingcontact.vue";
import modalcontact from "../../../components/Common/Modal/modalcontact.vue";
import modalattachment from "../../../components/Common/Modal/modalattachment.vue";
import $ from "jquery";
export default {
    components: {
        crmtimeline,
        crmnote,
        crmattachment,
        crmsendemail,
        crmcontacttab,
        modaltask,
        modalcall,
        modalevent,
        modalemail,
        modalexistingcontact,
        modalcontact,
        modalattachment
    },
    data(){
        return{
            isTimeline: false,
            isNotes: false,
            isAttachment: false,
            isSendEmail: false,
            isTask: false,
            isContract: false,
            iscontactDetails: null,
            showExistingContactModal: false,
            showContactModal: false,
            showAddTaskModal: false,
            showAddEventModal: false,
            showCallModal: false,
            LeftViewData: [],
            RightViewData: null,
            sendEmailModal: false,
            showAttachmentModal: false,
            attachmentData: [],
            activitiesData: null,
            timelinedata: [],
            noteData: [],
            productData: null,
            productHeaders: null,
            contactsData: [],
            contactsHeader: [],
            isShowNoteDialogue: null,
            noRecordForTab: false,
            EmailData: [],
            modalItem: null,
            modalTaskItem: null,
            modalFollowUpItem: null,
            modalCallItem: null,
            modalDeleteItem: null,
            contractName: '--',
            noteText: '',
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            PageSize: 50,
            isView: false,
            modal: null,
            stausName: false,
            isShowNotePopup: false,
            ApiURL: `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}&PageSize=50&pageNumber=1`,
            maxsize: "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize')",
            disallowedfiletypes: "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize'))",
            activeKey: 1,
            NoteListType: 'list',
            userid: null,
            companyId:null,
            email: null,
            containername:null,
            editurl: `/CRM/ManageContract/${this.$route.params.id}/CRM/CRM_CONTRACTS`,
            ApiURLEmail: `id=0&refId=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}`,
            isLoading:false,
            EmailInfo:null
        
        }
    },
    created:function(){
        this.userid=this.GetUserInfo.ID;
        this.companyId=this.GetUserInfo.CompanyId;
        this.email=this.GetUserInfo.Email;
        this.containername=this.GetUserInfo.ContainerName;
        var VueObj=this;
        VueObj.FetchData();
    },
    updated(){
    var vm = this; 
        setTimeout(function () { vm.setHeaderForSelectedTab() }, 500);      
  },
    methods:{
        DeleteActivity: function (item) {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("DeleteAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          'warning',
          true,
          this.$t("Alert")
        );
      } else {
        this.modalDeleteItem = item;
        this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
      }
    },
    DeleteActivityData: function (id) {
      var vm = this;
      if (id > 0) {
        talygen.confirm(vm.$t("ConfirmDelete"), function(result){
            if(result){
                var postJSON = {
              ids: id,
              moduleName: "CRM",
              subModuleCode: "CRM_CONTRACTS",
              refCode: "DELETE_VIEW_ACTIVITY",
              refId: vm.sourceId
            };
            var objectPostString = JSON.stringify(postJSON);
            ContractDataService.CommonDelete(objectPostString).then(
              function (response) {
                if (response.data[0].Status == "Success") {
                  vm.ShowAlert(vm.$t('DeletedSuccess',[vm.$t('CONTRACTS')]),"Success",true, vm.$t("Alert"));
                  vm.loadData("activities");
                } else {
                  vm.ShowAlert(vm.$t('DeletedError', [vm.$t('CONTRACTS')]), "failure", true, vm.$t("Alert"));
                }                 
              })
            }
            
            });
        }      
    },
        openCRMContactModal: function (contactDetails) {
            var type = 'AddContact';
            if (contactDetails != null) {
              type = 'UpdateContact';
            }
            if ((typeof this.checkForAdditionalPermission !== 'undefined') && !this.checkForAdditionalPermission(type)) {
              alertR(Globalize.localize("additionalOwnerAuthorityWarning"), true, Globalize.localize('Alert'));
            } else {
              this.iscontactDetails = contactDetails;
              this.showContactModal = (this.showContactModal) ? false : true;
            }
        },
        openCRMExistingContactModel: function () {
            if ((typeof this.checkForAdditionalPermission !== 'undefined') && !this.checkForAdditionalPermission("AddContact")) {
              alertR(Globalize.localize("additionalOwnerAuthorityWarning"), true, Globalize.localize('Alert'));
            } else {
              this.showExistingContactModal = (this.showExistingContactModal) ? false : true;
             }
        },
        openAddNewEvent: function () {
            this.showAddEventModal = (this.showAddEventModal) ? false : true;
        },
        openAddTaskModel: function () {
            this.showAddTaskModal = (this.showAddTaskModal) ? false : true;
        },
        openCRMAttachmentModal: function () {
            this.showAttachmentModal = (this.showAttachmentModal) ? false : true;
        },
        openCallEvent: function () {
            this.showCallModal = (this.showCallModal) ? false : true;
        },
        openActivityModal: function (item) {
            if (item.activity_type == "Task") {
                this.showAddTaskModal = true;
                this.modalTaskItem = item;
            }
            else if (item.activity_type == "FollowUp") {
                this.showAddEventModal = true;
                this.modalFollowUpItem = item;
            }
            else if (item.activity_type == "Call") {
                this.showCallModal = true;
                this.modalCallItem = item;
            }
        },
        openEmailModal: function (item) {
            this.sendEmailModal = true;
            this.modalItem = item;
        },
        openCRMEmailModal: function (item) {
            var type = 'AddEmail';
            if (item != null) {
                type = 'UpdateEmail';
            }
            this.sendEmailModal = (this.sendEmailModal) ? false : true;
            this.editid = item;
        },
        openShowNotePopup: function () {
                var ths = this;
                ths.isShowNotePopup = true;
                $("#tab-notes").trigger("click");
                ths.loadData("notes");
        },
        GetEmailInfo: function (item) {
            this.EmailInfo = item;
            this.sendEmailModal = (this.sendEmailModal) ? false : true;
        },
        DisableButton: function () {
            var vm = this;
            vm.isTimeline = false;
            vm.isNotes = false;
            vm.isAttachment = false;
            vm.isSendEmail = false;
            vm.isTask = false;
            vm.isContract = false;
        },
        FetchData: function () {
                var vm = this;
                vm.isLoading=true;
                ContractDataService.getQuickViewDetails(vm.ApiURL).then(function (response) {
                    vm.isLoading=false;
                    var fullLeftJSON = response.data.LEFTDATA;
                    var stausName = $.grep(fullLeftJSON, function (element, index) {
                        return element.name == 'status_id';
                    });
                    if (stausName != null && stausName.length > 0) {
                        vm.stausName = stausName[0];
                    }
                    fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                        return element.form_field_visibility == 'YES';
                    });
                    if (response.data.RIGHTDATA != null && response.data.RIGHTDATA.length > 0) {
                        var fullJSON = response.data.RIGHTDATA;
                        var gruopBy = "";
                        $.each(fullJSON, $.proxy(function (index, item) {
                            gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                            var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                                return element.groupKey == gruopBy;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullJSON, function (element, index) {
                                    return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        groupKey: gruopBy,
                                        groupData: returnedData
                                    };
                                    vm.timelinedata.push(objGroup);
                                }
                            }
                        }, vm));
                       
                        vm.noRecordForTab = false;
                    } else {
                        vm.isLoading=false;
                        vm.noRecordForTab = true;
                    }
                    if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                        var groupId = fullLeftJSON[0].group_id;
                        $.each(fullLeftJSON, $.proxy(function (index, item) {
                            if (item.name == 'contract_name') {
                                vm.contractName = item.value;
                            }
                            var chkGroup = $.grep(this.LeftViewData, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                    return element.group_id == groupId;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        LeftViewData: returnedData
                                    };
                                    vm.LeftViewData.push(objGroup);
                                }
                            }
                            groupId = item.group_id;
                        }, vm));
                    }
                  
                })
        },
        loadData: function (tabName,callFrom) {
            var vm = this;
            vm.DisableButton();
            if (tabName == "notes") {
                vm.isNotes = true;
                vm.activeKey = 2;
                if (callFrom && callFrom == "savenote") vm.isShowNotePopup = false;
            }
            else if (tabName == "attachments") {
                vm.isAttachment = true;
                vm.activeKey = 3;
            }
            else if(tabName=="timeline")
            {
                vm.isTimeline=true;
                vm.activeKey=1;
            }
             else if (tabName == "activities") {
                vm.isTask = true;
                vm.isTimeline = true;
                vm.activeKey = 4;
            }
            else if (tabName == "email") {
                vm.isSendEmail = true;
                vm.activeKey = 5;
            }
            else if (tabName == "contacts") {
                vm.isContract = true;
                vm.activeKey = 6;
            }
            if (tabName != "" && tabName != null) {
                if (tabName == "attachments") {
                    ContractDataService.AttachmentListing(vm.ApiURL).then(function (response) {
                        if (response.data != null) {
                            vm.attachmentData = response.data.data;
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "activities") {
                    ContractDataService.ActivityListing(vm.ApiURL).then(function (response) {
                        if (response.data != null && response.data.data.length > 0) {
                            vm.activitiesData = response.data.data;
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "contacts") {
                    ContractDataService.ContactListing(vm.ApiURL).then(function (response) {
                        if (response.data != null) {
                            vm.contactsData = response.data.data;
                            vm.contactsHeader = response.data.schema;
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                            vm.contactsData = null;
                        }
                    });
                }
                else if (tabName == "timeline") {
                    ContractDataService.TimelineListing(vm.ApiURL).then(function (response) {
                        if (response.data != null) {
                            vm.timelinedata = [];
                            var fullJSON = response.data.data;
                            var gruopBy = "";
                            $.each(fullJSON, $.proxy(function (index, item) {
                                gruopBy =vm.$options.filters.formatDate(item.Complete_datetime);
                                var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                                    return element.groupKey == gruopBy;
                                });
                                if (chkGroup.length == 0) {
                                    var returnedData = $.grep(fullJSON, function (element, index) {
                                        return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                                    });
                                    if (returnedData.length > 0) {
                                        var objGroup = {
                                            groupKey: gruopBy,
                                            groupData: returnedData
                                        };
                                        vm.timelinedata.push(objGroup);
                                    }
                                }
                            }, vm));
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "notes") {
                   vm.noteData = [];
                   ContractDataService.NoteListing(vm.ApiURL).then(function (response) {
                        if (response.data != null && response.data.data.length > 0) {
                            vm.noteData = response.data.data;
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "email") {
                   var url =vm.ApiURLEmail +vm.SortBy +"&sortExp=" +vm.SortExp +"&pageSize=" +vm.PageSize +"&pageNumber=" +vm.PageNumber;
                        ContractDataService.EmailListing(url).then(function (response) {
                        if (response.data != null) {
                            vm.EmailData = response.data.data;
                            setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
            }
        }
    }
}
</script>




